
@import url(./assets/icon.css);
body,html{margin:0;padding:0;font-family:sans-serif;font-size:10/16;color:#555
}
body{font-size:16px
}
h2,h3,h4{color:#35495E
}
h2{margin:2rem 0 1.5rem 0
}
h3{margin:0 0 1rem 0
}
h4{margin-bottom:0.5rem
}
section{width:80%;max-width:1000px;margin:0 auto 2rem auto
}
a{color:green;text-decoration:none
}
a:link{color:#069
}
a:hover{color:#000
}
ul,ol{margin:0;padding:0
}
nav.top-nav{background:#19232d;position:relative
}
nav.top-nav .title{display:none
}
nav.top-nav .v1x{display:inline-block;color:rgba(255,255,255,0.5);position:absolute;z-index:301;right:1rem;top:0.9rem;font-size:0.9rem
}
nav.top-nav .v1x:after{content:'This way to the';position:absolute;font-size:.8rem;text-transform:uppercase;top:-1.6rem;left:0;opacity:0.5
}
nav.top-nav .v1x a{color:rgba(255,255,255,0.6);transition:color .3s
}
nav.top-nav .v1x a:hover{color:rgba(255,255,255,0.8)
}
nav.top-nav.stick{position:fixed;top:0;left:0;right:0;z-index:300
}
nav.top-nav.stick .title{display:inline-block;color:rgba(255,255,255,0.8);position:absolute;z-index:301;left:1rem;top:.8rem
}
nav.top-nav.stick .v1x:after{content:'';display:none
}
nav.top-nav ul{list-style:none;padding:0;margin:0;display:-ms-flexbox;display:flex;-ms-flex-flow:row nowrap;flex-flow:row nowrap;-ms-flex-pack:center;justify-content:center;align-itrems:stretch
}
nav.top-nav ul li{margin:0 1rem;overflow:hidden
}
nav.top-nav ul li a{color:rgba(255,255,255,0.5);display:block;padding:.8rem .5rem;border-radius:5px;transition:color .3s;position:relative;overflow:hidden;z-index:1
}
nav.top-nav ul li a:hover{color:rgba(0,128,0,0.7);cursor:pointer
}
nav.top-nav ul li a.active{color:#009a00
}
nav.top-nav ul li a.active:after{content:'';display:block;width:0;height:0;border:10px solid transparent;border-bottom-color:#fff;position:absolute;bottom:-3px;left:calc(50% - 10px);z-index:3
}
#app{margin:0 auto
}
#app h1{margin-top:0rem;text-align:center;color:#fff
}
#app header{background:#35495e;height:120px
}
#app header nav ul{-webkit-display:flex;-moz-display:flex;display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between
}
#app header nav ul li{display:inline-block;list-style:none;background:#ccc;margin:0 auto;width:5rem;text-align:center;font-size:1.2rem;line-height:2.0rem;border-radius:0.2rem
}
#app aside{float:left;width:200px;margin:.5rem 2rem
}
#app article .main{border-left:1px dotted #626262
}
#app footer{position:fixed;bottom:0;height:50px;width:100%;text-aligh:center;line-height:50px;background:#19232d
}
.fade-enter-active,.fade-leave-active{transition:opacity .5s
}
.fade-enter,.fade-leave-active{opacity:0
}
.slide-fade-enter-active{transition:all .3s ease
}
.slide-fade-leave-active{transition:all 0.8s cubic-bezier(1, 0.5, 0.8, 1)
}
.slide-fade-enter,.slide-fade-leave-active{transform:translateX(10px);opacity:0
}
.bounce-enter-active{animation:bounce-in .5s
}
.bounce-leave-active{animation:bounce-out .5s
}
@keyframes bounce-in{
0%{transform:scale(0)
}
50%{transform:scale(1.5)
}
100%{transform:scale(1)
}
}
@keyframes bounce-out{
0%{transform:scale(1)
}
50%{transform:scale(1.5)
}
100%{transform:scale(0)
}
}
.app .btn{position:fixed;top:1rem;left:1.5rem;z-index:10;width:60px;height:40px;line-height:50px;border-radius:50%;border:none;outline:none;color:#fff;font-size:18px;line-height:2;text-align:center;background:#009a00;box-shadow:0 0 8px #FB90B7
}
.app .menu{position:fixed;top:9rem;left:9rem;width:50px;height:50px;border-radius:50%;transition:all .7s ease-in
}
.app.move-enter-active .inner{transform:translate3d(0, 0, 0);transition-timing-function:cubic-bezier(0, 0.57, 0.44, 1.97)
}
.app.move-enter-active .inner-1{transition-delay:.1s
}
.app.move-enter-active .inner-2{transition-delay:.2s
}
.app.move-enter-active .inner-3{transition-delay:.3s
}
.app.move-enter .inner,.app.move-leave-active .inner{transition-timing-function:ease-in-out
}
.app.move-enter .inner-1,.app.move-leave-active .inner-1{transform:translate3d(0, 60px, 0);transition-delay:.3s
}
.app.move-enter .inner-2,.app.move-leave-active .inner-2{transform:translate3d(40px, 40px, 0);transition-delay:.2s
}
.app.move-enter .inner-3,.app.move-leave-active .inner-3{transform:translate3d(60px, 0, 0);transition-delay:.1s
}
.app .inner{display:inline-block;position:absolute;width:30px;height:30px;line-height:30px;border-radius:50%;background:red;text-align:center;color:#fff;transition:all .4s
}
.app .inner-1{top:-7.0rem;left:1rem
}
.app .inner-2{left:0rem;top:-3rem
}
.app .inner-3{left:-3rem;top:0rem
}



